@import url(https://fonts.googleapis.com/css2?family=M+PLUS+1+Code:wght@500&family=Roboto+Slab:wght@300;400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  
    background-image: url(/Portfolio/static/media/bg.da88a605.jpg);
  
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{

  -webkit-backdrop-filter: blur(10px);

          backdrop-filter: blur(10px);
  width:  1vh;
}
.section0{
  background-color: red;
  height: 50000px;
  width: 1000px;
  -webkit-backdrop-filter: blur(2px);
          backdrop-filter: blur(2px);
}
.name{
  margin: 40px;
  font-size: 30px;
}
div{
  border-radius: 3px;
  border-bottom-right-radius: 10px;
  border: solid black 2px;
  -webkit-backdrop-filter: blur(10px);
          backdrop-filter: blur(10px);
}
.under{
  border-bottom-right-radius: 0px;
  border-bottom-left-radius: 10px;
  box-shadow: black -2px 2px;
  z-index: 1;
  width: 200px;
  background-color: rgba(218, 165, 32, 0.37);
  text-align: center;
  padding: 10px;
  font-size: 20px;
}
.under:hover{
  border-bottom-right-radius: 0px;
  border-bottom-left-radius: 10px;
  box-shadow: black -3px 3px 1px;
  z-index: 1;
  width: 230px;
  background-color: rgba(218, 165, 32, 0.37);
  text-align: center;
  padding: 10px;
  font-size: 22px;
}
.up{
  z-index: 2;
  width: 200px;
  background-color: rgba(255, 217, 0, 0.418);
  text-align: center;
  padding: 10px;
  font-size: 20px;
  box-shadow: black 6px 6px 5px;
}
.up:hover{
  z-index: 2;
  width: 230px;
  background-color: rgba(255, 217, 0, 0.418);
  text-align: center;
  padding: 10px;
  font-size: 22px;
  box-shadow: black 8px 8px 7px;
}
h1{
  margin:40px
}
h3{
  font-size: 50px;
}
hr{
  border: rgb(218, 165, 32) solid 2px;
}
.principal{
  font-size: 95px;
  margin: 270px 0;
}
.hide{
  position: relative;
  right: -500px;
  transition-duration: 1000ms;
  transition-property: all;
  transition-timing-function: ease-in;
}
.display{
  right: 0px;

}

h1,h2,h3{
  font-family: 'M PLUS 1 Code', sans-serif;
  color: gold;
  text-shadow: black -2px 2px 6px;
}
h5,h4{
  color: rgba(255, 233, 110, 0.979);
  text-shadow: black -1px 1px 9px;
  font-size: 30px;
}
h6{
  color: rgba(255, 233, 110, 0.979);
  text-shadow: black -1px 1px 9px;
  font-size: 20px;
}
#hrSec{
margin-top: 800px;
}
#contact{
  background-color: rgba(233, 204, 130, 0.404);
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  padding: 5px 20px;
  margin: 30px 0;
}
#proyects{
  background-color: rgba(233, 204, 130, 0.404);
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  padding: 0 20px;
  margin: 30px 0;
}
ul li{
  list-style: none;
  font-size: 20px;
}
section{
  font-size: 20px;
}
*{
  font-family: 'Roboto Slab', serif;
}
